
        @use './src/styles/base/mixins' as mixins;
        @use './src/styles/base/variables' as *;
      
.title {
  @include mixins.text-style(2rem, $color-primary, bold);
}

.text {
  @include mixins.text-style(1rem, $color-primary);
}

.subtext {
  @include mixins.text-style(0.875rem, $color-secondary);
}

.helptext {
  @include mixins.text-style(0.75rem, $color-error);
}
